<template>
  <div class="panel result"
      :class="{'panel-info': result.value.legal, 'panel-danger': !result.value.legal}">
    <div class="panel-heading"></div>
    <div class="panel-body">
      <div class="row">
        <div class="col-xs-4 result-label"></div>
        <div class="col-xs-8">{{legal}}</div>
      </div>
      <div class="row" v-if="result.value.overtimePay">
        <div class="col-xs-4 result-label"></div>
        <div class="col-xs-8">{{parseInt(result.value.overtimePay)}} </div>
      </div>
      <div class="row extra-leave" v-if="result.value.extraLeave">
        <div class="col-xs-4 result-label"></div>
        <div class="col-xs-8"></div>
      </div>

      <div class="violations" v-if="!result.value.legal">
        <div class="row">
          <div class="col-xs-4 result-label"></div>
          <div class="col-xs-8">
            <ul class="articles">
              <li v-for="(violation, index) in result.violations" :key="index">
                {{formatArticle(violation)}}
              </li>
            </ul>
          </div>
        </div>
        <div class="row">
          <div class="col-xs-4 result-label"></div>
          <div class="col-xs-8">
            <ul class="penalties">
              <li v-for="(penalty, index) in penalties" :key="index">
                <span v-html="formatPenalty(penalty)"></span>
              </li>
            </ul>
          </div>
        </div>
        <p class="note">
          
        </p>
      </div>
    </div>
  </div>

</template>

<script>
export default {
  props: ['result', 'penalties'],
  computed: {
    legal () {
      return this.result.value.legal ? '' : ''
    }
  },
  methods: {
    formatArticle (violation) {
      let output = `${violation.lawTitle}  ${violation.id} `
      if (violation.paragraph !== null && violation.paragraph !== undefined) {
        output += `  ${violation.paragraph + 1} `
      }
      return output
    },
    formatPenalty (penalty) {
      return penalty.possibilities.map(p => {
        let output = []
        if (p.fine) {
          let fineDesc = []
          if (p.fine.min) {
            fineDesc.push(` ${p.fine.min} `)
          }
          if (p.fine.max) {
            fineDesc.push(` ${p.fine.max} `)
          }
          output.push(` ${fineDesc.join('')}`)
        }
        if (p.imprisonment) {
          let unit
          let imprisonmentDesc = []
          if (p.imprisonment.unit === 'month') {
            unit = ' '
          } else if (p.imprisonment.unit === 'year') {
            unit = ' '
          }

          if (p.imprisonment.min) {
            imprisonmentDesc.push(` ${p.imprisonment.min} ${unit}`)
          }
          if (p.imprisonment.max) {
            imprisonmentDesc.push(` ${p.imprisonment.max} ${unit}`)
          }
          output.push(` ${imprisonmentDesc.join('')}`)
        }
        return output.join('')
      }).join(' <strong></strong> <br>')
    }
  }
}
</script>

<style scoped>
.note {
  margin-top: 10px;
}
.result {
  margin-top: 20px;
}

.result-label {
  font-weight: bold;
}

.articles, .penalties {
  padding-left: 0;
  list-style: none;
}
</style>



