<template>
  <div class="termination container">
    <form>
      <div class="form-group">
        <label class="control-label"></label>
        <div>
          <label class="radio-inline">
            <input type="radio" value="0" v-model="situation"> 
          </label>
          <label class="radio-inline">
            <input type="radio" value="1" v-model="situation"> 
          </label>
        </div>
      </div>

      <div class="form-group">
        <label for="end-date" class="control-label"></label>
        <input type="date" id="end-date" class="form-control" v-model="endDate">
      </div>

      <p> <strong>{{onboardString}}</strong> <strong>{{seniority}}</strong> </p>

      <p v-if="situation === '0'"> <strong> {{noticeDate}} ({{result.value.noticeDays}} )</strong>  <strong>{{result.value.severancePay}} </strong></p>

      <p v-else> <strong> {{noticeDate}} ({{result.value.noticeDays}} )</strong> </p>

    </form>

    <div class="alert alert-info" role="alert"> <a href="http://law.moj.gov.tw/LawClass/LawSingle.aspx?Pcode=N0030020&FLNO=12" target="_blank"> 12 </a>  <a href="
  http://law.moj.gov.tw/LawClass/LawSingle.aspx?Pcode=N0030001&FLNO=17" target="_blank"> 17 </a></div>

  </div>
</template>

<script>
import * as moment from 'moment'
import { mapState } from 'vuex'

import { Labor } from 'labor-standards-tw'

export default {
  data () {
    return {
      situation: '0',
      endDate: moment().format('YYYY-MM-DD')
    }
  },
  computed: {
    ...mapState(['onboard', 'monthlySalary']),
    onboardString () {
      return this.onboard.replace(/-/g, '/')
    },
    seniority () {
      return moment(this.endDate)
              .diff(moment(this.onboard), 'years', true)
              .toFixed(2)
    },
    result () {
      const labor = new Labor()
                        .onBoard(moment(this.onboard).toDate())
                        .setMonthlySalary(this.monthlySalary)
      return labor.beDismissed(moment(this.endDate).toDate())
    },
    noticeDate () {
      return moment(this.endDate)
              .subtract(this.result.value.noticeDays, 'days')
              .format('YYYY/MM/DD')
    }
  }
}
</script>

<style scoped>

.alert a {
  text-decoration: underline;
}

</style>



